@import "../../style/variables";
/* <!-- Link Swiper's CSS --> */
@import url("https://cdn.jsdelivr.net/npm/swiper@9/swiper-bundle.min.css");

.slider_container {
  background: #000000;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
  position: relative;
  height: 100%;
}
.slider {
  display: flex;
  align-items: center;
}

/* <!-- Demo styles --> */
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-button-next,.swiper-button-prev {
  color: aliceblue;
  background-color: transparent;
  

}



@media (max-width: 769px) {
  .swiper-button-next {
    display: none;
  }

  .swiper-button-prev {
    display: none;
  }
}
