@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#navbar_container {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 10vh;
  background-color: #001119;
}
#navbar_container .exitImg {
  width: 30px;
  background-color: transparent;
  cursor: pointer;
}
#navbar_container .search_bar {
  background-color: #0d1d25;
  border: none;
  color: #ffffff;
  width: 20%;
  border-radius: 5px;
}
#navbar_container .myorders {
  background-color: transparent;
  cursor: pointer;
}
#navbar_container #hamburger {
  display: none;
}

/* NAV -----------ADM--------- */
#navbar_container_adm {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-around;
  width: 100%;
  height: 65px;
  background-color: transparent;
}
#navbar_container_adm .exitImgAdm {
  width: 30px;
  background-color: transparent;
}

.logo_adm {
  display: flex;
  align-items: center;
}
.logo_adm p {
  color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  font-size: 1.5rem;
}

.search_bar_adm {
  background-color: #0d1d25;
  border: none;
  color: #ffffff;
  width: 30%;
  height: 40%;
  border-radius: 5px;
}

.hamburgerImgAdm {
  display: none;
}

/* Nav --------------- MOBILE */
@media screen and (max-width: 768px) {
  #navbar_container {
    display: flex;
    justify-content: space-around;
    width: 100vw;
    height: 65px;
    background-color: #001119;
    /* Nav -------ADM------ MOBILE */
  }
  #navbar_container .bars {
    background-color: transparent;
  }
  #navbar_container .search_bar {
    display: none;
  }
  #navbar_container .search_bar_menu {
    background-color: #0d161b;
    border: none;
    color: #ffffff;
    border-radius: 5px;
  }
  #navbar_container .search_bar_menu::-moz-placeholder {
    text-align: center;
  }
  #navbar_container .search_bar_menu::placeholder {
    text-align: center;
  }
  #navbar_container .exitImg {
    display: none;
  }
  #navbar_container #navbar_container_adm {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
  }
  #navbar_container #navbar_container_adm .search_bar_adm {
    display: none;
  }
  #navbar_container #navbar_container_adm .exitImgAdm {
    display: none;
  }
}/*# sourceMappingURL=Navbar.css.map */