@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");
.header {
  margin-top: 7%;
  margin-left: 20vw;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  height: 150px;
  background: linear-gradient(180deg, #091e26 0%, #00131c 100%);
}
.header img {
  margin-top: 80px;
  margin-left: -60px;
  max-width: 220px;
  transform: scaleX(-1);
}

.slogam {
  color: #c4c4cc;
  background-color: transparent;
}
.slogam :nth-child(1) {
  background-color: transparent;
  font-family: "Poppins", sans-serif;
  font-weight: medium;
  font-size: 1.7rem;
}
.slogam :nth-child(2) {
  background-color: transparent;
  font-family: "Poppins", sans-serif;
  font-weight: regular;
  font-size: 0.6rem;
}

@media screen and (max-width: 768px) {
  .header {
    width: 100vw;
    margin-right: 0;
    margin-left: 0;
    gap: 5px;
  }
  .header img {
    margin-left: 2%;
    width: 200px;
  }
  .slogam :nth-child(2) {
    background-color: transparent;
    width: 80%;
    font-family: "Poppins", sans-serif;
    font-weight: regular;
    font-size: 0.6rem;
  }
}/*# sourceMappingURL=Home.css.map */